body {
  --color-background: #fafafa;
  --color-foreground: #1f2023;
  margin: 0;
}

p {
  font-size: 22px;
}

.dark {
  --color-background: #1f2023;
  --color-foreground: #efefef;
}

html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  color: var(--color-foreground);
  background-color: var(--color-background);
  position: absolute;
  z-index: -2;
  width: 100%;
  overflow-x: hidden;
}

/**************** LOADING PAGE ****************/
/* body {
  height: 100%;
  overflow-y: hidden;
} */

body.showBody {
  height: auto;
  overflow-y: auto;
}

.loader {
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Courier New", monospace;
  color: #000;
}

.loader.hide {
  display: none;
}

.flex-container {
  display: flex;
}

.flex-vertical-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.status-svg {
  display: block;
  margin: auto 8px;
  fill: red;
}

.status-svg.online {
  fill: green;
}

.terminal-text {
  text-align: left;
}

.terminal-input {
  margin-left: 0.5em;
  font-family: "Courier New", monospace;
}

.loading-bar {
  height: 10px;
  margin: 1em auto;
  background-color: rgb(239, 239, 239);
  border-radius: 5px;
}

.loading-animation {
  width: 0%;
  height: 100%;
  border-radius: 5px;
  background-color: rgb(9, 132, 227);
  transition: width 2s ease-in;
}

.loading-animation.start {
  width: 100%;
}

.line {
  opacity: 0;
  transition: opacity 0s linear 0.5s;
}

.line.start {
  opacity: 1;
}

.finish-loading-bar {
  position: absolute;
  left: 50%;
  width: 0%;
  height: 10px;
  margin-top: -10px;
  background-color: rgb(9, 132, 227);
  border-radius: 5px;
  transition: all 1.5s ease-in;
}

.finish-loading-bar.start {
  width: 100%;
  left: 0;
}

.loading-top,
.loading-bottom {
  position: absolute;
  height: 50%;
  width: 100%;
  background-color: white;
}

.loading-top {
  bottom: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: bottom 2s ease-in-out;
}

.loading-top.start {
  bottom: 100%;
}

.loading-bottom {
  top: 50%;
  transition: top 2s ease-in-out;
}

.loading-bottom.start {
  top: 100%;
}

/**************** GENERAL CSS ****************/
.section-full {
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 720px) {
  html {
    font-size: 14px;
  }
}

@media only screen and (max-width: 480px) {
  html {
    font-size: 12px;
  }
}

.section-header {
  font-size: 2rem;
  margin: 0;
  opacity: 0;
  transform: translateY(100%);
  transition: all 1s 0.5s ease-in-out;
}

.animate-header {
  transform: translateY(0);
  opacity: 1;
}

/**************** NAVIGATION CSS ****************/
.nav-burger {
  position: fixed;
  top: 16px;
  left: 16px;
  z-index: 2;
}

.nav-burger div,
.nav-burger div::before,
.nav-burger div::after {
  background-color: var(--color-foreground);
}

.side-nav-bar {
  height: 100%;
  position: fixed;
  background-color: var(--color-background);
  width: 0;
  z-index: 1;
  top: 0px;
  left: 0px;
  overflow-x: hidden;
  padding-top: 20px;
  transition: width 0.5s ease-in-out;
  border: #000 solid 1px;
  box-shadow: 5px 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.side-nav-bar.open {
  width: 250px;
}

.nav-container {
  max-width: 250;
  width: "100%";
}

.nav-border {
  border-bottom: var(--color-foreground) solid 1px;
}

.navigation-button {
  height: 4rem;
  width: 100%;
  box-shadow: inset 0 0 #fff;
  color: rgb(34, 139, 230);
  transition: all 0.5s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navigation-button:hover {
  box-shadow: inset 300px 0 2px 0 rgb(34, 139, 230);
  color: #fff;
}

a.navigation-button {
  text-decoration: none;
  font-size: 20px;
}

.color-toggle {
  -webkit-tap-highlight-color: transparent;
  overflow: hidden;
  position: relative;
  border-radius: 32px;
  background-color: #000;
  border: 1px solid #dee2e6;
  height: 28px;
  width: 60px;
  min-width: 60px;
  margin: 0 4px 4px auto;
  transition-property: background-color, border-color;
  transition-timing-function: ease;
  transition-duration: 150ms;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 6px;
  font-weight: 600;
}

.color-toggle:before {
  z-index: 1;
  border-radius: 32px;
  box-sizing: border-box;
  content: "";
  display: block;
  background-color: #fff;
  height: 20px;
  width: 20px;
  border: 1px solid rgb(222, 226, 230);
  transition: transform 150ms ease 0s;
  transform: translateX(5px);
}

.color-toggle:after {
  position: absolute;
  z-index: 0;
  display: flex;
  height: 100%;
  -webkit-box-align: center;
  align-items: center;
  line-height: 0;
  right: 30%;
  transform: translateX(14px);
  content: url('data:image/svg+xml; utf8, <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-sun" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="%23FAEA35" fill="%23FAEA35" stroke-linecap="round" stroke-linejoin="round">  <path stroke="none" d="M0 0h24v24H0z" fill="none"></path>  <circle cx="12" cy="12" r="4"></circle>  <path d="M3 12h1m8 -9v1m8 8h1m-9 8v1m-6.4 -15.4l.7 .7m12.1 -.7l-.7 .7m0 11.4l.7 .7m-12.1 -.7l-.7 .7"></path></svg>');
  color: rgb(255, 255, 255);
  transition: color 150ms ease 0s;
}

.color-toggle:checked {
  background-color: rgb(0, 0, 0, 0.75);
}

.color-toggle:checked::before {
  transform: translateX(35px);
  background-color: #fff;
}

.color-toggle:checked:after {
  transform: translateX(-100%);
  content: url('data:image/svg+xml; utf8, <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-moon" width="16" height="16" viewBox="0 0 24 24" stroke-width="2" stroke="white" fill="white" stroke-linecap="round" stroke-linejoin="round">  <path stroke="none" d="M0 0h24v24H0z" fill="none"/>   <path d="M12 3c.132 0 .263 0 .393 0a7.5 7.5 0 0 0 7.92 12.446a9 9 0 1 1 -8.313 -12.454z" /></svg>');
  color: rgb(255, 255, 255);
}

@media only screen and (max-width: 600px) {
  .side-nav-bar.open {
    width: 100%;
  }

  .nav-burger {
    top: 8px;
    left: 8px;
  }

  .navigation-button:hover {
    box-shadow: inset 600px 0 2px 0 rgb(34, 139, 230);
    color: #fff;
  }
}

/**************** HOME CSS ****************/
.home-container {
  margin: 3rem auto;
  display: flex;
  gap: 24px;
}

.text-container {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.text-container * {
  text-align: left;
}

.intro-text {
  font-size: 4rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 0;
  margin-bottom: 1.5rem;
  color: transparent;
  width: 100%;
  background: linear-gradient(90deg, var(--color-background) 0%, var(--color-foreground) 15%);
  background-clip: text;
  background-repeat: no-repeat;
  background-size: 80%;
  animation: shine 1.5s linear forwards;
}

@keyframes shine {
  0% {
    background-position-x: -500%;
  }
  100% {
    background-position-x: -100%;
  }
}

.intro-role-container {
  height: auto;
  position: relative;
}

.intro-role-animation {
  width: 0%;
  background-color: rgb(9, 132, 227);
  position: absolute;
  height: 100%;
  animation: roleAnimation 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) 1s forwards;
}

@keyframes roleAnimation {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}

.intro-role-text {
  margin: 0;
  opacity: 0;
  animation: roleAnimationFadeIn 2s 3s forwards;
}

@keyframes roleAnimationFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.anchor-button {
  background-color: rgb(9, 132, 227);
  color: #fff;
  height: 48px;
  border-radius: 32px;
  font-weight: 600;
  font-size: 14px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 16px;
  padding-right: 22px;
  opacity: 0;
  transform: translateY(50px);
  animation: fadeIn 1s 4s forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.anchor-button:hover {
  background-color: #1c7ed6;
}

#home-canvas {
  z-index: -1;
  position: absolute;
  left: 0;
}

@media only screen and (max-width: 880px) {
  .intro-text {
    background-size: 90%;
    font-size: 3.5rem;
    animation: shineTablet 1.5s linear forwards;
  }

  @keyframes shineTablet {
    0% {
      background-position-x: -800%;
    }
    100% {
      background-position-x: -100%;
    }
  }
}

@media only screen and (max-width: 630px) {
  .home-container {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .intro-text {
    animation: shineMobile 1.5s linear forwards;
  }

  @keyframes shineMobile {
    0% {
      background-position-x: -800%;
    }
    100% {
      background-position-x: 0%;
    }
  }

  .text-container {
    justify-content: center;
  }

  .text-container * {
    text-align: center;
  }
}

@media only screen and (max-width: 420px) {
  .intro-text {
    font-size: 3rem;
  }
}

/**************** AVATAR ANIMATION ****************/

.arm-wave {
  transform: translateY(100%);
  transition: transform 1s ease-in-out;
}

.arm-wave.start {
  animation: armWave 2s ease-in-out 2 1s;
  transform: translateY(0%);
  transition: transform 1s ease-in-out;
}

@keyframes armWave {
  0% {
    transform: rotate(0deg);
    transform-origin: 100%;
  }
  50% {
    transform: rotate(5deg);
    transform-origin: 100%;
  }
  100% {
    transform: rotate(0deg);
    transform-origin: 100%;
  }
}

/**************** ABOUT CSS ****************/
.about-container {
  padding: 3rem 9rem 12rem;
  color: white;
  background-color: rgb(9, 132, 227);
}

.about-text {
  opacity: 0;
  transform: translateY(-100%);
  transition: all 1s 1s ease-in-out;
}

.animate-about-text {
  opacity: 1;
  transform: translateY(0);
}

.about-container p {
  font-size: 1.5rem;
}

/**************** PROJECTS CSS ****************/
.projects-container {
  background-color: var(--color-background);
  border-radius: 12px;
  box-shadow: 0px 0px 5px #e6ecf8, 0 0 0 2px #e6ecf8;
  color: var(--color-foreground);
  display: block;
  padding: 1.25rem;
  margin: -10rem auto 0;
}

.project-logo {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.website-preview {
  width: 100%;
  height: 75%;
  border-radius: 20px;
}

.bg-image {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-position 2s ease-out;
}

.website-preview:hover .bg-image {
  background-position: 0 100%;
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
}

.fade-enter-active,
.fade-exit-active {
  transition: opacity 100ms;
}

@media only screen and (max-width: 768px) {
  .projects-container {
    margin: -10rem 3rem 0;
  }

  .project-logo-container {
    display: none;
  }

  .project-text-container {
    flex-basis: 100%;
    max-width: 100%;
  }
}

.project-component {
  opacity: 0;
  transition: all 1s ease-in-out;
}

.project-component.left {
  transform: translateX(-50%);
}

.project-component.right {
  transform: translateX(50%);
}

.animate-project {
  opacity: 1;
  transform: none !important;
}

/**************** CONTACT CSS ****************/
.contact-parent-container {
  padding: 2px 9rem 1rem;
}

.contact-container {
  margin: -5rem auto 0;
  padding: 1rem;
  color: white;
  background-color: #141c3a;
  border-radius: 24px;
}

.contact-icon-container {
  border-color: white;
  color: white;
}

.contact-icon-container:hover {
  background-color: white;
}

.contact-icon-container:hover > .contact-icon {
  color: rgb(9, 132, 227) !important;
}

.contact-icon {
  display: flex;
  justify-content: center;
  color: white;
}

@media only screen and (max-width: 768px) {
  .about-container {
    padding-left: 3rem;
    padding-right: 3rem;
  }
}
